import {React, useState, useContext, useEffect} from 'react';
import {Link} from 'react-router-dom';
import './sign-in-form.style.scss';
import {loginUser} from "../../action/user-action";
import { UserContext } from '../../contexts/user.context';
import { useNavigate } from 'react-router-dom';

const defaultFormFields = {
  email: '',
  password: '',
};

const SignInForm = () => {
  const navigate = useNavigate();
  //Manage user's email and password by useState()
  const [formFields, setFormFields] = useState(defaultFormFields);
  const { email, password } = formFields;

  //take currentUser data from context
  const { setCurrentUser } = useContext(UserContext);
  const { setDisplayUserName } = useContext(UserContext);
  const { currentUser } = useContext(UserContext);
  //when user enter email or password field
  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormFields({...formFields, [name]: value });
  }
 
  //when user click login button
  const handleSubmit = async(e) => {
    
    console.log("handleSubmit")

    e.preventDefault();

    //response user's email and pwd
    await loginUser({
      email,
      password,
    }).then((response) =>{
      
      if (response.data.success === true){
        //success sign in -> move to projectList
        console.log("login success");
        setCurrentUser(formFields);
        console.log('current user : ',currentUser);
        setDisplayUserName(currentUser.email.substring(0, email.indexOf("@")));
        
        navigate('/projectList')
      }
      else{
        //failed login
        alert("    ");
        console.log("login fail");
      }
    })
  }

  return (
    <form className='sign-in-form' onSubmit={handleSubmit}>
      <h2> !</h2>
      <input placeholder="" type="email" name="email" required onChange={handleChange} value={email}/>
      <input placeholder="" type="password" name="password" required onChange={handleChange} value={password}/>
      <button class="custom-btn sign-in-btn">
        <span></span>
      </button>
      <p> ? 
        <Link className="sign-up-link" to='/sign-up'>
          
        </Link>
      </p>
    </form>
  );
};

export default SignInForm;
