import { useState } from 'react';
import SignUpButton from '../button/sign-up-button.component';
import { registerUser } from "../../action/user-action";
import { useNavigate } from "react-router-dom";


const defaultFormFields = {
  email: '',
  password: '',
  confirmPassword:'',
  phoneNumber:'',
};

const SignUpForm = () => {
  const history = useNavigate();
  const [formFields, setFormFields] = useState(defaultFormFields);
  const { email,password,confirmPassword,phoneNumber,userName } = formFields;

  const handleChange = (e) => {
    const {name, value} = e.target;
    setFormFields({...formFields, [name] : value});
    console.log({formFields});
  }

  const handleSubmit = async (e) => {
    console.log('  X ')
    console.log('!');
    //when password and confirmPassword are diffrent
    if (password !== confirmPassword){
      alert('  ')
      e.preventDefault();
    }
    else {
      //send user data to server
      await registerUser({
        email,
        password,
        phoneNumber,
        userName,
      }).then((res) => {
        if (res.status === 200) {
          console.log('send data ', formFields );
          history('/');
        }
      }).catch((e) => console.log(e));

    }
  }
  
  return(
    <div className="sign-up-form">
      <h2></h2>
      <input placeholder="" type="email" name="email" required onChange={handleChange} value={email}/>
      <input placeholder="" type="password" name="password" required onChange={handleChange} value={password}/>
      <input placeholder=" " type="password" name="confirmPassword" required onChange={handleChange} value={confirmPassword}/>
      <input placeholder=" " type="text" name="phoneNumber" required onChange={handleChange} value={phoneNumber}/>
      <input placeholder="" type="text" name="userName" required onChange={handleChange} value={userName}/>
      <SignUpButton onClickButton={() => handleSubmit()}/>
    </div>
  )
}

export default SignUpForm